.home-page {
  padding: 1.5rem;
  padding-bottom: 100px;
  min-height: 100vh;
}

.home-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.notification-btn {
  position: relative;
  width: 44px;
  height: 44px;
  border-radius: 12px;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}

.notification-badge {
  position: absolute;
  top: -4px;
  right: -4px;
  background: #ef4444;
  color: white;
  font-size: 0.75rem;
  font-weight: 700;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.summary-cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  margin-bottom: 2rem;
}

.summary-card {
  padding: 1.5rem;
  border-radius: 16px;
  display: flex;
  gap: 1rem;
  align-items: center;
}

.summary-icon {
  width: 48px;
  height: 48px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.summary-content {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.summary-content span {
  font-size: 0.875rem;
}

.summary-content h3 {
  font-size: 1.25rem;
  font-weight: 700;
}

.recent-expenses {
  margin-bottom: 2rem;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.section-header h2 {
  font-size: 1.25rem;
  font-weight: 700;
}

.add-btn {
  width: 44px;
  height: 44px;
  border-radius: 12px;
  border: none;
  color: white;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}

.add-btn:hover {
  transform: scale(1.05);
}

.expense-list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.expense-item {
  padding: 1rem;
  border-radius: 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.expense-info {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.expense-info h4 {
  font-size: 1rem;
  font-weight: 600;
}

.expense-info span {
  font-size: 0.875rem;
}

.expense-amount {
  font-size: 1.125rem;
  font-weight: 700;
}

.empty-state {
  text-align: center;
  padding: 3rem 1rem;
}

.notifications-panel {
  position: fixed;
  top: 80px;
  right: 1.5rem;
  width: calc(100% - 3rem);
  max-width: 400px;
  border-radius: 16px;
  padding: 1rem;
  z-index: 1000;
  animation: slideDown 0.3s ease-out;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.notifications-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(139, 92, 246, 0.2);
}

.notifications-header button {
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: inherit;
}

.notification-item {
  padding: 1rem;
  border-radius: 12px;
  background: rgba(239, 68, 68, 0.1);
  margin-bottom: 0.75rem;
}

.notification-item h4 {
  font-size: 0.875rem;
  font-weight: 600;
  margin-bottom: 0.25rem;
  color: #ef4444;
}

.notification-item p {
  font-size: 0.875rem;
  color: #ef4444;
}
