.cards-page {
  padding: 1.5rem;
  padding-bottom: 100px;
  min-height: 100vh;
}

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

.page-header h1 {
  font-size: 1.75rem;
  font-weight: 700;
}

.add-card-btn {
  width: 44px;
  height: 44px;
  border-radius: 12px;
  border: none;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}

.add-card-btn:hover {
  transform: scale(1.05);
}

.cards-selector {
  display: flex;
  gap: 0.75rem;
  overflow-x: auto;
  padding-bottom: 0.5rem;
  margin-bottom: 1.5rem;
}

.cards-selector::-webkit-scrollbar {
  height: 4px;
}

.card-chip {
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  border: none;
  font-weight: 600;
  white-space: nowrap;
  transition: all 0.2s;
}

.card-chip:hover {
  transform: translateY(-2px);
}

.card-details {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.card-stats {
  padding: 1.5rem;
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.stat-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.stat-row span:first-child {
  font-size: 0.875rem;
}

.stat-row span:last-child {
  font-size: 1.125rem;
}

.add-expense-btn {
  width: 100%;
  padding: 1rem;
  border-radius: 12px;
  border: none;
  color: white;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  transition: all 0.2s;
}

.add-expense-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(139, 92, 246, 0.3);
}
