.logo {
  display: flex;
  align-items: center;
  font-weight: 800;
  letter-spacing: -0.02em;
}

.logo-sm {
  font-size: 1.25rem;
}

.logo-md {
  font-size: 1.5rem;
}

.logo-lg {
  font-size: 2rem;
}

.logo-m,
.logo-c {
  background: linear-gradient(135deg, #a78bfa, #8b5cf6);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.logo-text {
  color: var(--text-primary);
}
